import React from 'react'
//import clsx from 'clsx'
import { v4 as uuid } from 'uuid'

import { LAYOUT } from '../config/config'
import i18n from '../i18n'

import Button from '@material-ui/core/Button'

import Grid from '@material-ui/core/Grid'
import List from '@material-ui/core/List'
import ListItem from '@material-ui/core/ListItem'
import ListItemIcon from '@material-ui/core/ListItemIcon'
import ListItemText from '@material-ui/core/ListItemText'

import IconButton from '@material-ui/core/IconButton'

import TableBody from '@material-ui/core/TableBody'
import TableRow from '@material-ui/core/TableRow'
import TableCell from '@material-ui/core/TableCell'

import Divider from '@material-ui/core/Divider'

import AddIcon from '@material-ui/icons/AddCircle'
import EditIcon from '@material-ui/icons/Edit'
import CancelIcon from '@material-ui/icons/Block'
import SaveIcon from '@material-ui/icons/Check'
import DeleteIcon from '@material-ui/icons/Delete'

import EnhancedTable from '../components/EnhancedTable'
import Dialog from '../components/Dialog'
import Inputs from '../components/Inputs'


/*
 * STYLES
 */
import { withStyles } from '@material-ui/core/styles'
const styles = theme => ({
	deleteButton: {
		marginTop: '25px'
	},
	hr: {
		margin: '10px 2px'
	},
	CustomTextInput: {
		width: 150
	},
	TableCell: {
		verticalAlign: 'top',
		padding: '5px 0'
	}
});


class SettingsPage extends React.Component {
	
	constructor(props) {
		super(props);
		
		this.state = {
			popup: false,
			
			selectedDevice: null,
			selectedKey: null,
			
			devices: this.props.devices || {},
			errors: {}
		}
		
		this.onChange = this.props.onChange;
		this.groupEditor = {};
	}
	
	/**
	 * 
	 */
	_addDevice = () => {
		let deviceId = uuid();
		console.log(deviceId);
		
		this.setState(prevState => ({
			selectedDevice: deviceId,
			devices: {
				[deviceId]: {
					name: '',
					function: '',
					options: {},
					attributes: {},
					states: {}
				},
				...prevState.devices
			}
		}), () => console.log(this.state.devices));
	}
	
	/**
	 * 
	 */
	_addKey = (deviceId, key, name) => {
		let prevDevice = { ...this.state.devices[deviceId] }
		prevDevice[key] = prevDevice[key] || {};
		prevDevice[key][name] = key === 'states' ? {} : i18n.t('new ' + key.substr(0, key.length-1));
		
		this.setState(prevState => ({
			devices: {
				...prevState.devices,
				[deviceId]: prevDevice
			}
		}));
	}
	
	/**
	 * 
	 */
	_cancelDevice = (deviceId, type, stateKey) => {
		this.groupEditor = {};
		this.setState({ selectedDevice: null });
	}
	
	/**
	 * 
	 */
	_closePopup = action => {
		
		if (action === 'cancel') {
			this.setState({ popup: false });
		}
		else {
			let errors = {};
			['device-key'].forEach(key => errors[key] = !this.groupEditor[key]);
			
			if (Object.values(errors).indexOf(true) > -1) {
				this.setState(prevState => ({ errors: { ...prevState.errors, ...errors }}));
			}
			else {
				this.setState({
					...this._addKey(this.state.selectedDevice, this.state.selectedKey, this.groupEditor['device-key']),
					popup: false
				});
			}
		}
	}
	
	/**
	 * 
	 */
	_deleteDevice = (deviceId, type, stateKey) => {
		
		// delete key from states, options or attributes (type)
		if (type) {
			let prevDevice = { ...this.state.devices[deviceId] }
			delete prevDevice[type][stateKey];
			
			// delete from editor
			if ((';' + Object.keys(this.groupEditor).join(';')).indexOf(';' + type + '-' + stateKey + '-') > -1) {
				
				for (const key in this.groupEditor) {
					
					if (key.indexOf(type + '-' + stateKey + '-') > -1) {
						delete this.groupEditor[key];
					}
				}
			}
			
			// delete from device
			this.setState(prevState => ({
				devices: {
					...prevState.devices,
					[deviceId]: {
						options: {},
						attributes: {},
						states: {},
						...prevState.devices[deviceId],
						[type]: prevDevice[type]
					}
				}
			}));
		}
		
		// delete device
		else {
			this.groupEditor = {};
			let prevDevices = { ...this.state.devices }
			delete prevDevices[deviceId];
			
			this.setState({ devices: prevDevices });
		}
	}
	
	/**
	 * 
	 */
	_openPopup = (deviceId, key) => () => {
		this.groupEditor['device-key'] = '';
		this._saveDevice(deviceId);
		
		this.setState({
			popup: true,
			selectedDevice: deviceId,
			selectedKey: key
		});
	}
	
	/**
	 * 
	 */
	_saveDevice = deviceId => {
		let prevDevice = { ...this.state.devices[deviceId] }
		
		for (const key in this.groupEditor) {
			const [ type, stateKey, label ] = key.split('-');
			const val = this.groupEditor[key];
			
			if (label) {
				prevDevice[type] = prevDevice[type] || {};
				prevDevice[type][stateKey][label] = val;
			}
			else if (!label && type === 'device' && stateKey !== 'key') {
				prevDevice[stateKey] = val;
			}
		}
		
		this.setState(prevState => ({
			selectedDevice: null,
			selectedKey: null,
			
			devices: {
				...prevState.devices,
				[deviceId]: prevDevice
			}
		}), () => this.onChange(this.state.devices));
	}
	
	/*
	 *
	 */
	renderDeviceAttribute(deviceId, attribute) {
		const { classes } = this.props;
		
		return (

<ListItem key={'AttributesListItem-' + attribute.key} dense>
	<ListItemIcon>
		<IconButton onClick={() => this._deleteDevice(deviceId, 'attributes', attribute.key)}>
			<DeleteIcon />
		</IconButton>
	</ListItemIcon>
	<ListItemText disableTypography>
		<Inputs.TextInput
			key={'attributes-' + attribute.key}
			id={'attributes-' + attribute.key}
			onChange={(id, userInput) => this.groupEditor[id] = userInput}
			classes={{ CustomTextInput: classes.hr }}
			shrink={true}
			label={attribute.key}
			val={attribute.val || ''}
			/>
			
	</ListItemText>
</ListItem>

		);
	}
	
	/*
	 *
	 */
	renderDeviceAttributes(deviceId, attributes) {
		return Object.keys(attributes || {}).map(attributeKey => {
			let attribute = attributes[attributeKey];
			attribute = typeof attribute === 'string' ? attribute : (Array.isArray(attribute) ? attribute.join(',') : JSON.stringify(attribute, null, 0));
			return this.renderDeviceAttribute(deviceId, { val: attribute, key: attributeKey });
		});
	}
	
	/*
	 *
	 */
	renderDeviceOption(deviceId, option) {
		const { classes } = this.props;
		
		return (

<ListItem key={'JarvisListItem-' + option.key} dense>
	<ListItemIcon>
		<IconButton onClick={() => this._deleteDevice(deviceId, 'options', option.key)}>
			<DeleteIcon />
		</IconButton>
	</ListItemIcon>
	<ListItemText disableTypography>
		<Inputs.TextInput
			key={'options-' + option.key}
			id={'options-' + option.key}
			onChange={(id, userInput) => this.groupEditor[id] = userInput}
			classes={{ TextInput: classes.hr }}
			shrink={true}
			label={option.key}
			val={option.val || ''}
			/>
		
	</ListItemText>
</ListItem>

		);
	}
	
	/*
	 *
	 */
	renderDeviceOptions(deviceId, options) {
		return Object.keys(options || {}).map(optionKey => {
			let option = options[optionKey];
			option = typeof option === 'string' ? option : (Array.isArray(option) ? option.join(',') : JSON.stringify(option, null, 0));
			
			if (option === '' || option === '{}' || (Array.isArray(option) && option.length === 0)) {
				return null;
			}
			
			return this.renderDeviceOption(deviceId, { val: option, key: optionKey });
		});
	}
	
	/*
	 *
	 */
	renderDeviceState(deviceId, state) {
		const { classes } = this.props;
		
		return (

<React.Fragment key={'StatesListItemLabelFragment-' + state.key}>
	<ListItem key={'StatesListItemLabel-' + state.key} dense>
		<ListItemIcon>
			<IconButton size="small" onClick={() => this._deleteDevice(deviceId, 'states', state.key)}>
				<DeleteIcon />
			</IconButton>
		</ListItemIcon>
		<ListItemText primary=<strong>{state.key}</strong> />
		
	</ListItem>
	<ListItem key={'StatesListItemInputs-' + state.key} dense divider>
		<ListItemText disableTypography>
			<Inputs.TextInput id={'states-' + state.key + '-label'} onChange={(id, userInput) => this.groupEditor[id] = userInput} classes={{ TextInput: classes.hr }} shrink={true} label="Label" val={state.label || ''} />
			<Inputs.TextInput id={'states-' + state.key + '-state'} onChange={(id, userInput) => this.groupEditor[id] = userInput} classes={{ TextInput: classes.hr }} shrink={true} label="State" val={!state.state || typeof state.state === 'string' ? (state.state || '') : state.state.node} />
			<Inputs.TextInput id={'states-' + state.key + '-action'} onChange={(id, userInput) => this.groupEditor[id] = userInput} classes={{ TextInput: classes.hr }} shrink={true} label="Action" val={!state.action || typeof state.action === 'string' ? (state.action || '') : state.action.node} />
		</ListItemText>
	</ListItem>
</React.Fragment>

		);
	}
	
	/*
	 *
	 */
	renderDeviceStates(deviceId, states) {
		return Object.keys(states || {}).map(stateKey => {
			let state = states[stateKey];
			state = typeof state === 'string' ? { 'state': state } : state;
			return this.renderDeviceState(deviceId, { ...state, key: stateKey });
		});
	}
	
	/*
	 *
	 */
	renderDevice(device) {
		const { classes } = this.props;
		
		device.id = device.key;
		const states = this.renderDeviceStates(device.id, device.states || {});
		const jarvis = this.renderDeviceOptions(device.id, device.options || {});
		const attributes = this.renderDeviceAttributes(device.id, device.attributes || {});
		
		return (

<TableRow
	key={device.id + '-' + device.function}
	role="checkbox"
	tabIndex={-1}
	>
	
	<TableCell padding="checkbox" scope="row" align="center">
		<IconButton size="small" onClick={() => this._saveDevice(device.id)}>
			<SaveIcon />
		</IconButton>
		<IconButton size="small" onClick={() => this._cancelDevice(device.id)}>
			<CancelIcon />
		</IconButton>
		
		<IconButton size="small" onClick={() => this._deleteDevice(device.id)} classes={{ root: classes.deleteButton }}>
			<DeleteIcon />
		</IconButton>
	</TableCell>
	
	<TableCell classes={{ root: classes.TableCell }}>
		<Inputs.TextInput
			classes={{ CustomTextInput: classes.CustomTextInput, TextInput: classes.hr }}
			key="device-name"
			id="device-name"
			onChange={(id, userInput) => this.groupEditor[id] = userInput}
			label={i18n.t('Device')}
			val={device.name}
			/>
			
	</TableCell>
	
	<TableCell classes={{ root: classes.TableCell }}>
		<Inputs.TextInput
			classes={{ CustomTextInput: classes.CustomTextInput, TextInput: classes.hr }}
			key="device-function"
			id="device-function"
			onChange={(id, userInput) => this.groupEditor[id] = userInput}
			label={i18n.t('Function')}
			val={device.function}
			/>
			
	</TableCell>
	
	{/* STATES */}
	<TableCell classes={{ root: classes.TableCell }}>
		<Grid container>
			<Grid item xs={12}>
				<Button onClick={this._openPopup(device.id, 'states')} startIcon={<AddIcon />}>{i18n.t('add new state')}</Button>
			</Grid>
			<List dense>
				{states}
			</List>
		</Grid>
	</TableCell>
	
	{/* JARVIS */}
	<TableCell classes={{ root: classes.TableCell }}>
		<Grid container>
			<Grid item xs={12}>
				<Button onClick={this._openPopup(device.id, 'options')} startIcon={<AddIcon />}>{i18n.t('add new option')}</Button>
				<Divider classes={{ root: classes.hr }} />
			</Grid>
			<Grid item xs={12}>
				<List dense>
					{jarvis}
				</List>
			</Grid>
		</Grid>
	</TableCell>
	
	{/* ATTRIBUTES */}
	<TableCell classes={{ root: classes.TableCell }}>
		<Grid container>
			<Grid item xs={12}>
				<Button onClick={this._openPopup(device.id, 'attributes')} startIcon={<AddIcon />}>{i18n.t('add new attribute')}</Button>
				<Divider classes={{ root: classes.hr }} />
			</Grid>
			<Grid item xs={12}>
				<List dense>
					{attributes}
				</List>
			</Grid>
		</Grid>
	</TableCell>
	
</TableRow>

		)
	}
	
	/*
	 *
	 */
	renderDevices(props) {
		const { rows } = props;
		
		return (
			<TableBody>

			{rows.map((row, index) => {
				let options = Object.keys(row.options || {}).filter(optionKey => (row.options[optionKey] === '' || (Array.isArray(row.options[optionKey]) && row.options[optionKey].length === 0) || (typeof row.options[optionKey] === 'object' && Object.keys(row.options[optionKey]).length === 0)) === false).sort().join(', '); 
				let attributes = Object.keys(row.attributes || {}).sort().join(', ');
				
				return this.state.selectedDevice === row.key ? this.renderDevice(row) :
				
				<TableRow key={'TableRow-' + index} hover tabIndex={-1} onClick={() => this.setState({ selectedDevice: row.key })}>
					
					<TableCell padding="checkbox" scope="row" align="center">
						<IconButton size="small" onClick={() => this.setState({ selectedDevice: row.key })}>
							<EditIcon />
						</IconButton>
					</TableCell>
					<TableCell>{row.name}</TableCell>
					<TableCell>{row.function}</TableCell>
					<TableCell>{Object.keys(row.states || {}).join(', ')}</TableCell>
					<TableCell>{options.length > 30 ? options.substr(0,27) + '...' : options}</TableCell>
					<TableCell>{attributes.length > 15 ? attributes.substr(0,12) + '...' : attributes}</TableCell>
				</TableRow>
				
			})}
		
			</TableBody>

		);
	}
	
	/*
	 *
	 */
	render() {
		const devices = Object.keys(this.state.devices).map(deviceId => ({ ...this.state.devices[deviceId], key: deviceId }));
		
		return (

<React.Fragment>
	<EnhancedTable
		title="Devices"
		headCells={[
			{ id: 'edit', width: '3%', disablePadding: true, label: '' },
			{ id: 'name', width: '12%', label: i18n.t('Name') },
			{ id: 'function', width: '10%', label: i18n.t('Function') },
			{ id: 'states', width: '50%', disablePadding: false, label: i18n.t('States') },
			{ id: 'jarvis', width: '13%', disablePadding: false, label: i18n.t('Jarvis') },
			{ id: 'attributes', width: '12%', disablePadding: false, label: i18n.t('Attributes') }
		]}
		rows={devices}
		
		defaultOrderBy="name"
		selectable={false}
		sortable={true}
		stickyHeader
		
		CustomTableBody={props => this.renderDevices(props)}
		toolbarRightActionButton={
			<Inputs.Button onClick={() => this._addDevice()} startIcon={<AddIcon />} disabled={this.state.selectedDevice !== null} label={i18n.t('add new device')} />
		}
		/>
		
	<Dialog
		header={i18n.t('Add new entry')}
		actions={[
			<Button key='action-cancel' onClick={() => this._closePopup('cancel')} color="secondary">
				{i18n.t('Cancel')}
			</Button>,
			<Button key='action-save' onClick={() => this._closePopup('save')} color="primary">
				{i18n.t('Ok')}
			</Button>
		]}
		
		maxWidth={'lg'}
		open={this.state.popup}
		onClose={() => this._closePopup('cancel')}
		>
		
		<List>
			<ListItem divider>
				<Inputs.TextInput
					key='device-key'
					id='device-key'
					{...LAYOUT.deviceKey}
					onChange={(id, userInput) => this.groupEditor[id] = userInput}
					required={true}
					error={this.state.errors['device-key']}
					autoFocus
					/>
					
			</ListItem>
		</List>
		
	</Dialog>
	
</React.Fragment>

		)
	}
}

export default withStyles(styles)(SettingsPage);
