import React from 'react'

import StateList, { ModuleConfig as StateListConfig } from './StateList'


export default function StateListHorizontal(props) {
	return (<StateList horizontal={true} {...props} />);
}


export const ModuleDescription = "Zeigt eine Liste von States horizontal an";
export const ModuleScreenshots = ['StateListHorizontal1.png'];

export const ModuleConfig = StateListConfig