import React, { useState } from 'react'
import clsx from 'clsx'

import i18n from '../i18n'

import Grid from '@material-ui/core/Grid'
import Tooltip from '@material-ui/core/Tooltip'
import Autocomplete from '@material-ui/lab/Autocomplete'
import InputButton from '@material-ui/core/Button'
import FormControl from '@material-ui/core/FormControl'
import FormControlLabel from '@material-ui/core/FormControlLabel'
import FormHelperText from '@material-ui/core/FormHelperText'
import InputAdornment from '@material-ui/core/InputAdornment'
import InputLabel from '@material-ui/core/InputLabel'
import MenuItem from '@material-ui/core/MenuItem'
import Select from '@material-ui/core/Select'
import Switch from '@material-ui/core/Switch'
import TextField from '@material-ui/core/TextField'

import AddIcon from '@material-ui/icons/AddCircle'
import InfoIcon from '@material-ui/icons/Info'


/*
 * STYLES
 */
import { makeStyles } from '@material-ui/core/styles'
const useStyles = makeStyles(theme => ({
	fullWidth: {
		width: '100%',
	},
	formControl: {
		display: 'inline-flex',
		width: 250,
		marginLeft: theme.spacing(1),
		marginRight: theme.spacing(1),
		padding: '0 !important',
	},
	InputLabel: {
		width: 330,
	},
	Switch: {
		marginTop: '10px'
	},
	SwitchSmall: {
		marginTop: '2px'
	},
	SwitchInline: {
		marginLeft: '5px'
	},
	TextInput: {
	},
	CustomTextInput: {
	},
}));


function InfoIconWrapper(props) {
	const { children, id, info, link } = props;
	const classes = useStyles(props);
	
	const tooltip = link ? <><a target="_blank" rel="noopener noreferrer" href={link}><InfoIcon color="primary" /></a></> : <InfoIcon color="primary" />;
	
	return (

<Grid
	key={'grid-' + id}
	className={classes.formControl}
	container
	item
	>
	
	{info && <Grid item style={{ width: 30 }}>
		<Tooltip title={i18n.t(info)}>
			{tooltip}
		</Tooltip>
	</Grid>}
	<Grid item style={{ width: info ? 250-30 : '100%' }}>
		{children}
	</Grid>
</Grid>

	);
}

function Button(props) {
	const { id, label, color, variant, disabled, startIcon, onClick } = props;
	const classes = useStyles(props);
	
	return (

<InfoIconWrapper {...props}>
	<InputButton
		key={id}
		classes={{ root: classes.fullWidth }}
		variant={variant || 'contained'}
		color={color || 'primary'}
		disabled={disabled}
		startIcon={startIcon || <AddIcon />}
		onClick={onClick}
		>
		
		{i18n.t(label)}
	</InputButton>
</InfoIconWrapper>

	)
}


function TextAutoCompleteInput(props) {
	const { id, val, autoCompleteValues, autoComplete, autoHighlight, onChange } = props;
	
	
	const [value, setValue] = useState(val || '');
	const handleItemChange = (event, val) => onChange(id, val);
	const handleTextChange = (event, val) => {
		let userInput = val || (event && event.target && event.target.value) || '';
		setValue(userInput);
	}
	
	return (

<Autocomplete
	id={'TextFieldAutoComplete-' + id}
	options={autoCompleteValues || []}
	
	value={value}
	onChange={handleTextChange}
	onInputChange={handleItemChange}
	
	autoComplete={autoComplete}
	autoHighlight={autoHighlight}
	
	renderInput={(params) => TextInput({ ...props, autoCompleteParams: params })}
	/>

	)
}


function TextInput(props) {
	const { id, icon, label, type, val, required, error, autoFocus, shrink, autoCompleteParams, placeholder, helperText, onChange, disableUnderline, InputProps } = props;
	const classes = useStyles(props);
	
	
	const [value, setValue] = useState(val || '');
	const handleTextChange = event => {
		let userInput = (event && event.target && event.target.value) || '';
		
		setValue(userInput);
		onChange(id, userInput);
	}
	
	let InputType = 'text';
	if (type === 'NumberInput') {
		InputType = 'number';
	}
	
	return (

<InfoIconWrapper {...props}>
	<TextField
		key={'TextField-' + id}
		classes={{ root: clsx(classes.fullWidth, classes.TextInput, classes.CustomTextInput) }}
		
		label={i18n.t(label)}
		type={InputType}
		placeholder={placeholder || ''}
		helperText={helperText || ''}
		autoFocus={autoFocus}
		
		onChange={handleTextChange}
		value={value}
		required={required}
		error={error}
		
		InputLabelProps={{
			shrink: shrink
		}}
		
		InputProps={{
			disableUnderline: disableUnderline,
			startAdornment: icon && (
				<InputAdornment position="start">
					<span className={'mdi mdi-' + icon}></span>
				</InputAdornment>
			),
			...InputProps
		}}
		
		{...autoCompleteParams}
		/>
	
</InfoIconWrapper>

	)
}


function NumberInput(props) {
	return TextInput({ ...props, type: 'NumberInput' });
}


function SelectInput(props) {
	const { id, label, options, val, helperText, onChange } = props;
	const classes = useStyles(props);
	
	const [value, setValue] = useState(val || '');
	const handleSelectChange = event => {
		let userInput = (event && event.target && event.target.value) || '';
		
		setValue(userInput);
		onChange(id, userInput);
	}
	
	const menuOptions = Object.keys(options || {}).map(option => <MenuItem key={'MenuItem-' + id + '-' + option} value={option}>{options[option]}</MenuItem>);
	
	return (

<InfoIconWrapper {...props}>
	<FormControl key={'FormControl-' + id}>
		<InputLabel>{i18n.t(label)}</InputLabel>
		<Select
			key={'Select-' + id}
			className={classes.input}
			value={value}
			onChange={handleSelectChange}
			>
			
			{menuOptions}
		</Select>
		<FormHelperText>{helperText || ''}</FormHelperText>
	</FormControl>
</InfoIconWrapper>

	)
}


function SwitchInlineInput(props) {
	const { id, label, val, size, disabled, onChange } = props;
	const classes = useStyles(props);
	
	const [checked, setChecked] = useState(val || false);
	const handleSwitchChange = event => {
		let userInput = (event && event.target && event.target.checked) || false;
		
		setChecked(userInput);
		onChange(id, userInput);
	}
	
	return (

<InfoIconWrapper {...props}>
	<FormControlLabel
		classes={{ root: classes.SwitchInline }}
		label={i18n.t(label)}
		control={<Switch
			classes={{ root: size === 'small' ? classes.SwitchSmall : classes.Switch }}
			checked={checked}
			disabled={disabled}
			onChange={handleSwitchChange}
			size={size} />}
		/>
		
</InfoIconWrapper>

	)
}

function SwitchInput(props) {
	const { id, label, val, size, disabled, onChange } = props;
	const classes = useStyles(props);
	
	const [checked, setChecked] = useState(val || false);
	const handleSwitchChange = event => {
		let userInput = (event && event.target && event.target.checked) || false;
		
		setChecked(userInput);
		onChange(id, userInput);
	}
	
	return (

<InfoIconWrapper {...props}>
	<FormControl key={'FormControl-' + id}>
		<InputLabel classes={{ root: classes.InputLabel }} shrink={true}>{i18n.t(label)}</InputLabel>
		<Switch
			classes={{ root: size === 'small' ? classes.SwitchSmall : classes.Switch }}
			checked={checked}
			disabled={disabled}
			onChange={handleSwitchChange}
			size={size}
			/>
		
	</FormControl>
</InfoIconWrapper>

	)
}


export default {
	Button,
	NumberInput,
	SelectInput,
	SwitchInput,
	SwitchInlineInput,
	TextInput,
	TextAutoCompleteInput
}
