import React from 'react'

import { DEFAULTS } from '../config/config'
import Settings from '../Settings'
import Connection from '../Connection'
import i18n from '../i18n'

import Button from '@material-ui/core/Button'
import Paper from '@material-ui/core/Paper'
import Tabs from '@material-ui/core/Tabs'
import Tab from '@material-ui/core/Tab'
import TextField from '@material-ui/core/TextField'
import Typography from '@material-ui/core/Typography'

import Dialog from '../components/Dialog'
import Inputs from '../components/Inputs'

import DevicesPage from '../pages/DevicesPage'
import LayoutsPage from '../pages/LayoutsPage'
import SettingsPage from '../pages/SettingsPage'


/*
 * STYLES
 */
import { withStyles } from '@material-ui/core/styles'
const styles = theme => ({
	pinToTop: {
		position: 'absolute',
		top: 0,
	},
	title: {
		paddingLeft: theme.spacing(2),
		paddingRight: theme.spacing(1),
		marginBottom: theme.spacing(1),
		minHeight: '64px',
		display: 'flex',
		position: 'relative',
		alignItems: 'center'
	},
	rawEditor: {
		fontFamily: 'Roboto Mono,Consolas,Courier New,monospace !important'
	}
});


class ConfigurationPage extends React.Component {
	
	constructor(props) {
		super(props);
		
		let settings = { ...Settings.getSettings() };
		let layout = settings.layout;
		delete settings.layout;
		delete settings.iobroker;
		
		this.groups = [];
		Object.keys(this.props.devices).forEach(key => this.groups = this.groups.concat(this.props.devices[key].options.group || []));
		this.groups = [...new Set(this.groups)].sort();
		
		
		this.state = {
			expertMode: false,
			tab: 'devices',
			
			settings: settings || {},
			layout: layout || {},
			devices: this.props.devices || {}
		}
		
		this.socket = Connection.getConnection;
		
		this._onDevicesChange.bind(this);
		this._onSettingsChange.bind(this);
		this._onLayoutsChange.bind(this);
	}
	
	/**
	 * 
	 */
	_onDevicesChange = devices => this.setState({ devices: devices });
	_onSettingsChange = settings => this.setState({ settings: settings });
	_onLayoutsChange = (layout, settings) => this.setState(prevState => ({ settings: { ...prevState.settings, ...settings || {} }, layout: layout }));
	
	/**
	 * 
	 */
	_handleTabChange = (event, tab) => this.setState({ tab });
	
	/**
	 *
	 */
	_cancel = () => {
		
		// restore old devices
		
		
		// restore old settings
		this.setState({ settings: Settings.getSettings() });
		
		// close popup
		this.props.navigate('home');
	}
	
	/**
	 *
	 */
	_save = () => {
		let result = [
			this.socket.setState(DEFAULTS.NODE_SETTINGS, JSON.stringify({ ...this.state.settings, layout: this.state.layout })),
			this.socket.setState(DEFAULTS.NODE_DEVICES, JSON.stringify(this.state.devices))
		];
		
		Promise.all(result)
			.then(() => window.location.reload())
			.catch(err => console.error(err));
	}
	
	/*
	 *
	 */
	render() {
		const { classes } = this.props;
		
		return (

<Dialog
	classes={{ CustomDialog: classes.pinToTop }}
	fullWidth={true}
	maxWidth={'xl'}
	
	open={(this.props.page === 'configuration')}
	onClose={this._cancel}
	
	header={[
		<Typography key="header" variant="h6">
			Configuration
			<Inputs.SwitchInlineInput
				key="Input-expertMode"
				id="expertMode"
				size="small"
				label={i18n.t('Expert mode')}
				onChange={(key, val) => this.setState({ expertMode: val })}
				val={this.state.expertMode}
				/>
		</Typography>,
		
		<Tabs
			key="tabs"
			value={this.state.tab}
			indicatorColor="primary"
			textColor="primary"
			onChange={this._handleTabChange}
			>
			
			<Tab value="devices" label={i18n.t('Devices')} />
			{this.state.expertMode === true && <Tab value="devicesRaw" label={[i18n.t('Raw Editor'), <br key='tab' />, i18n.t('Devices') ]} />}
			<Tab value="layout" label={i18n.t('Layout')} />
			{this.state.expertMode === true && <Tab value="layoutRaw" label={[i18n.t('Raw Editor'), <br key='tab' />, i18n.t('Layout') ]} />}
			<Tab value="settings" label={i18n.t('Settings')} />
			{this.state.expertMode === true && <Tab value="settingsRaw" label={[i18n.t('Raw Editor'), <br key='tab' />, i18n.t('Settings') ]} />}
		</Tabs>
	]}
	actions={[
		<Button key='action-cancel' autoFocus onClick={this._cancel} color="secondary">
			{i18n.t('Cancel')}
		</Button>,
		<Button key='action-save' onClick={this._save} color="primary">
			{i18n.t('Save')}
		</Button>
	]}
	>
	
	<div hidden={this.state.tab !== 'devices'}>
		<Paper>
			{this.state.tab === 'devices' && <DevicesPage devices={this.state.devices} onChange={this._onDevicesChange} />}
			
		</Paper>
		
	</div>
	{this.state.expertMode === true && 
	<div hidden={this.state.tab !== 'devicesRaw'}>
		<Paper className={classes.paper}>
			<Typography className={classes.title} variant="h6">
				{i18n.t('Raw Editor') + ' ' + i18n.t('Devices')}
			</Typography>
			<TextField
				key='TextField-Raw-Devices'
				InputProps={{ className: classes.rawEditor }}
				multiline
				fullWidth
				onChange={event => this._onDevicesChange(JSON.parse((event && event.target && event.target.value) || '{}'))}
				value={JSON.stringify(this.state.devices, null, 3)}
				/>
			
		</Paper>
	</div>}
	<div hidden={this.state.tab !== 'settings'} style={{ width: '100%' }}>
		<Paper className={classes.title}>
			<Typography variant="h6">
				{i18n.t('Settings')}
			</Typography>
		</Paper>
		{this.state.tab === 'settings' && <SettingsPage settings={this.state.settings} onChange={this._onSettingsChange} />}
		
	</div>
	{this.state.expertMode === true && 
	<div hidden={this.state.tab !== 'settingsRaw'} style={{ width: '100%' }}>
		<Paper className={classes.paper}>
			<Typography className={classes.title} variant="h6">
				{i18n.t('Raw Editor') + ' ' + i18n.t('Settings')}
			</Typography>
			<TextField
				key='TextField-Raw-Settings'
				InputProps={{ disableUnderline: true, className: classes.rawEditor }}
				multiline
				fullWidth
				onChange={event => this._onSettingsChange(JSON.parse((event && event.target && event.target.value) || '{}'))}
				value={JSON.stringify(this.state.settings, null, 3)}
				/>
			
		</Paper>
	</div>}
	<div hidden={this.state.tab !== 'layout'}>
		<Paper>
			<Typography className={classes.title} variant="h6">
				{i18n.t('Layout')}
			</Typography>
		</Paper>
		{this.state.tab === 'layout' && <LayoutsPage groups={this.groups} settings={this.state.settings} layout={this.state.layout} onChange={this._onLayoutsChange} />}
		
	</div>
	{this.state.expertMode === true && 
	<div hidden={this.state.tab !== 'layoutRaw'} style={{ width: '100%' }}>
		<Paper className={classes.paper}>
			<Typography className={classes.title} variant="h6">
				{i18n.t('Raw Editor') + ' ' + i18n.t('Layout')}
			</Typography>
			<TextField
				key='TextField-Raw-Layout'
				InputProps={{ disableUnderline: true, className: classes.rawEditor }}
				multiline
				fullWidth
				onChange={event => this._onLayoutsChange(JSON.parse((event && event.target && event.target.value) || '{}'))}
				value={JSON.stringify(this.state.layout || {}, null, 3)}
				/>
			
		</Paper>
	</div>}
	
</Dialog>

		)
	}
}

export default withStyles(styles)(ConfigurationPage);
